}

.film-poster__button svg {
  fill: #000;
  transition: transform .4s cubic-bezier(.4, 0, .2, 1);
}

.film-poster__button svg:hover path, .film-poster__button svg :focus path {
  fill: #000;
  fill-opacity: 1;
  stroke: #000;
  stroke-width: 1px;
  stroke-dashoffset: 0;
  stroke-dasharray: 3000;
  animation: 7s ease-in-out animate;
}

.film-poster .dark-mode-svg {
  fill: #fff;
  transition: transform .4s cubic-bezier(.4, 0, .2, 1);
}

.film-poster .dark-mode-svg:hover path, .film-poster .dark-mode-svg :focus path {
  fill: #fff;
  fill-opacity: 1;
  stroke: #fff;
  stroke-width: 1px;
  stroke-dashoffset: 0;
  stroke-dasharray: 3000;
  animation: 7s ease-in-out animatedark;
}

@keyframes animatedark {
  0% {
    stroke-dashoffset: 3000px;
    fill: none;
  }

  100% {
    stroke-dashoffset: 0;
    fill: #fff;
  }
}

.film-poster__genre {
