}

.film-poster__year {
  color: #ff6b08;
  font-weight: 800;
}

.film-poster--dark-mode {
  box-shadow: 0 2px 10px #fdfbfb40;
}

@keyframes animate {
  0% {
    stroke-dashoffset: 3000px;
    fill: none;
  }

  100% {
    stroke-dashoffset: 0;
    fill: #000;
  }
}

.spinner-box {
  opacity: 1;
  transition: transition-properties(opacity), transition-properties(visibility);
  visibility: visible;
  z-index: 20;
  background-color: #2f303ab3;
  justify-content: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.spinner {
  background: #0000004d;
  border-radius: 5px;
  width: 100px;
  height: 30px;
  margin: auto;
  position: relative;
  overflow: hidden;
  box-shadow: 0 35px 0 -5px #aaa, 0 -5px #ddd, 0 -25px 0 -5px #fff, -25px -30px #ddd, -25px 30px #ddd, 25px -30px #ddd, 25px 30px #ddd, 20px 10px 0 5px #ddd, 20px -10px 0 5px #ddd, -20px -10px 0 5px #ddd, -20px 10px 0 5px #ddd;
}

@media screen and (width >= 48em) {
  .spinner {
    transform: scale(1.5);
  }
}

@media screen and (width >= 80em) {
  .spinner {
    transform: scale(2);
  }
}

.spinner:after, .spinner:before {
  content: "";
  box-sizing: border-box;
  border: 4px dashed #fff;
  border-radius: 100%;
  width: 35px;
  height: 35px;
  animation: 4s linear infinite tape;
  display: block;
  position: absolute;
  bottom: -4px;
  transform: rotate(0);
}

.spinner:before {
  right: 0;
  box-shadow: 0 0 0 4px #fff, 0 0 0 34px #000;
}

.spinner:after {
  left: 0;
  box-shadow: 0 0 0 4px #fff, 0 0 0 65px #000;
}

@keyframes tape {
  0% {
    transform: rotate(0)scale(.4);
  }

  100% {
    transform: rotate(-360deg)scale(.4);
  }
}

.spinner-box--hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.icon-moon, .icon-sun, .icon-moon--visible, .icon-sun--visible {
  opacity: 1;
  fill: #fff;
  width: 23px;
  height: 23px;
  animation: .3s ease-in-out fadeInOut;
  display: inline;
}

.dark-mode-button {
  background-color: #0000;
  border: none;
  width: -moz-fit-content;
  width: fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.icon-moon--hidden, .icon-sun--hidden {
  opacity: 0;
  display: none;
}

.body--dark {
